body{
    overflow-y: scroll;
}
/* Main page */
.page-footer{
    height: 50px;
    background-color: #007cba;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border-top: 3px solid #005a87;
}
.page-content{
    height: calc(100vh - 200px);
    background-color: white;
    flex-direction: row;
    display: flex;
    justify-content: center;
}
/* Header */
.page-header{
    height: 150px;
    background-color: white;
    display: flex;
    flex-direction: column;
}
.header-content{
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.navigation{
    height: 50px;
    min-height: calc(5vh - 6px);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #007cba;
    border-top: 3px solid #005a87;
    border-bottom: 3px solid #005a87;
}/* Content page*/
.content-area{
    padding-top: 80px;
    width: 800px;
    height: auto;
    padding-bottom: 80px;
}
.page-header button{
    width: 140px;
    height: 44px;
    min-height: calc(5vh - 6px);
    font-weight: 400;
    color: white;
    font-size: 14px;
    border: none;
    border-radius: 0px;
    background-color: #007cba;
    outline: none;
}
.page-header button:hover{
    background-color: #009cda;
}
.page-header button:focus{
    background-color: #008cca;
}
.page-header button:focus:hover{
    background-color: #009cda;  
}
/* Footer */
.page-footer button{
    width: 130px;
    height: 5vh;
    font-weight: 400;
    color: white;
    font-size: 14px;
    border: none;
    border-radius: 0px;
    background-color: #007cba;
}
.page-footer button:hover{
    background-color: #009cda;
}
/*------------------------------------------*/
/*------------------------------------------*/
.panel{
    width: 100%;
    padding-top: 30px;
    display: flex;
    justify-content: center;
    overflow-y: scroll !important;
}
.contentp{
    width: 800px;
    display: flex;
    flex-direction: column;
}
.time{
    color: black !important;
}
.panel h3{
    margin-top: 2px;
    margin-bottom: 2px;
}
.panel .filter .menubutton{
    height: 30px;
    width: 20%;
    margin: 2px;
}
.panel .menubutton{
    height: 30px;
    width: 20%;
    margin-inline: 2px;
}
.panel .row{
    width: 100%;
    margin-bottom: 0px;
}
.panel .input-bx{
    height: 24px;
    width: 185px;
    font-size: 18px;
    border: solid 2px #005a87;;
    border-radius: 2px;
    margin-right: 7px;
}
.panel #table{
    margin-top: 10px;
    width: 795px;
    height: 150px;
    overflow-y: scroll;
    overflow-x: hidden;
    display: flex;
    align-items: flex-start;
    border: solid 2px #005a87;
}
.panel .tc{
    text-align: left;
    width: 190px;
    padding-left: 2.5px;
}
.panel .buttons .sel{
    width: 350px;
}
