body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 79.5vh;
  align-items: center;
  justify-content: center;
}
.small-container{
  background-color: white;
  position: relative;
  width: 350px;
  height: 400px;
  border-radius: 15px;
  border: solid 5px #005a87;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.content{
  position: relative;
  width: 275px;
  height: 300px;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  align-items: left;
  padding-top: 20px;
}
.menubutton{
  width: 100px;
  height: 50px;
  color: #fff;
  border: solid 2px #005a87;
  border-radius: 3px;
  background-color: #007cba;
  padding: 5px;
  text-align: center;
  font-weight: bold;
}
.menubutton:hover{  
  border: solid 2px #005a87;
  background-color: #005a87;
}
.name{
  padding-bottom: 3px;
  font-size: 18px;
}
.buttons{
  display: flex;
  flex-direction: row;
  width: 100%;
}
.input-box{
  width: 267px;
  height: 30px;
  font-size: 18px;
  border: solid 2px;
  border-radius: 3px;
  padding-left: 7px;
}
.row{
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 6px;
}
#table{
  margin-top: 10px;
  width: 730px;
  height: 150px;
  overflow-y: scroll;
  overflow-x: hidden;
  display: flex;
  align-items: flex-start;
  border: solid 2px #005a87;
}
.tc{
  text-align: left;
  width: 180px;
  padding-left: 2.5px;
}